	module.exports = {"data":{"site":{"siteMetadata":{"title":"JSToElm","subtitle":"A show about learning Elm, Functional Programing, and generally leveling up as a JS developer.","copyright":"© All rights reserved.","menu":[{"label":"Episodes","path":"/episodes","localPath":true},{"label":"iTunes","path":"https://itunes.apple.com/us/podcast/javascript-to-elm/id1272026213?mt=2","localPath":null},{"label":"RSS","path":"http://jstoelm.libsyn.com/rss","localPath":null},{"label":"GooglePlay","path":"http://jstoelm.libsyn.com/gpm","localPath":null},{"label":"Overcast","path":"https://overcast.fm/itunes1272026213/javascript-to-elm","localPath":null}],"author":{"name":"JavaScript To Elm","email":"contact@jstoelm.com","twitter":"@jstoelm","rss":"rss.xml"}}},"allContentfulEpisode":{"edges":[{"node":{"audioUrl":"e028-ports-a-multiplying","id":"c7MXvIls59YCugAQ4Qg6S8Y","title":"28: Ports Multiplying","draft":false,"published":"2018-03-08","slug":"28-ports-multiplying","summary":{"id":"c7MXvIls59YCugAQ4Qg6S8YsummaryTextNode","internal":{"content":"We are back to meow notes, adding our onClick event from Elm back to React to route either to a new note, or an existing note by note.id. This is going to require us to poke another hole in our ports, but remember Murphy's warning, not to treat this like http methods, so lets keep that in mind as we move forward. "}},"article":{"id":"c7MXvIls59YCugAQ4Qg6S8YarticleTextNode","internal":{"type":"contentfulEpisodeArticleTextNode"}}}},{"node":{"audioUrl":"e027-state-of-elm","id":"c7jgu5aAh3yScOSAC8wEsCq","title":"27: State of Elm","draft":false,"published":"2018-03-01","slug":"27-state-of-elm","summary":{"id":"c7jgu5aAh3yScOSAC8wEsCqsummaryTextNode","internal":{"content":"I thought it would be great to talk about the current state of Elm, with the yearly survey closing in a few days, talk about what previous year’s results have told us, go over what the questions for this year and how I answered them, and make any predictions for the out come."}},"article":{"id":"c7jgu5aAh3yScOSAC8wEsCqarticleTextNode","internal":{"type":"contentfulEpisodeArticleTextNode"}}}},{"node":{"audioUrl":"e026-ports-json-elm-render","id":"c4N7Bsa5MEU6mQ0MgyqEwmy","title":"26: Ports, JSON, And Elm render","draft":false,"published":"2018-02-22","slug":"26-ports-json-and-elm-render","summary":{"id":"c4N7Bsa5MEU6mQ0MgyqEwmysummaryTextNode","internal":{"content":"The last 10%. We left off our app with being able to pass the return JSON from our fetch promise to get a list of notes through ports to Elm, and debug logging that out. At this point I thought, \"success!\". You might even recall, I glossed over rendering it to a list, totally spaced the click event needed to make the list actually do something. So today we take the work of getting the data to work, to the work needed to get it back to working functionality using Elm. "}},"article":{"id":"c4N7Bsa5MEU6mQ0MgyqEwmyarticleTextNode","internal":{"type":"contentfulEpisodeArticleTextNode"}}}}]}},"pathContext":{"group":[{"node":{"parent":{"id":"Episode"},"id":"c4N7Bsa5MEU6mQ0MgyqEwmy","title":"26: Ports, JSON, And Elm render","slug":"26-ports-json-and-elm-render","summary":{"id":"c4N7Bsa5MEU6mQ0MgyqEwmysummaryTextNode","internal":{"content":"The last 10%. We left off our app with being able to pass the return JSON from our fetch promise to get a list of notes through ports to Elm, and debug logging that out. At this point I thought, \"success!\". You might even recall, I glossed over rendering it to a list, totally spaced the click event needed to make the list actually do something. So today we take the work of getting the data to work, to the work needed to get it back to working functionality using Elm. "}},"article":{"id":"c4N7Bsa5MEU6mQ0MgyqEwmyarticleTextNode"},"audioUrl":"e026-ports-json-elm-render"}}],"pathPrefix":"episodes","first":false,"last":true,"index":3,"pageCount":3,"slug":"index"}}

